<template>
  <div class="header-container">
    <div class="header w">
      <div class="header-nav">
        <ul>
          <li v-for="item in navItems">
            <Router-link :to="item.link">{{item.text}}</Router-link>
          </li>
        </ul>
      </div>
      <div class="header-search">
        <input type="search" placeholder="站内搜索">
        <button>搜索</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Header",
  data(){
    return {
      navItems:[
        {
          link:"/home",
          text:"首页"
        },
        {
          link:"/mood",
          text:"心情随笔"
        },
        {
          link:"/memorydir",
          text:"韶华追忆"
        },
        {
          link:"/techdir",
          text:"技术分享"
        },
        {
          link:"/books",
          text:"书屋"
        },
        {
          link:"/blogmessage",
          text:"blog留言"
        },
        {
          link: "/aboutme",
          text: "关于我"
        }
      ]
    }
  }
}
</script>

<style scoped>
.header-container {
  width: 100%;
  min-width: 800px;
  background-color: #b5bce8;
}
.header {
  display: flex;
  justify-content: space-between;
  height: 60px;
  min-width: 800px;
  font-size: 20px;
  line-height: 60px;
}
.header .header-nav ul li {
  float: left;
  padding: 0 0 0 16px;
}
.header .header-nav ul li a {
  color: #366;
}
.header .header-search {
  position: relative;
  width: 24%;
  min-width: 180px;
  height: 32px;
  top: 12px;
  background-color: #fff;
  border: 2px solid pink;
  border-radius: 6px;
  overflow: hidden;
}
.header .header-search input,.header .header-search button {
  float: left;
  height: 28px;
  font-size: 16px;
  color: #333;
}
.header .header-search input {
  width: 80%;
  padding-left: 5px;
}
.header .header-search button {
  padding: 1px;
  width: 20%;
  background-color: #eee;
  border-left: none;
}
</style>



