import Vue from 'vue'
import Router from 'vue-router'
import MemoryDirectory from "../components/MemoryDirectory";
const HelloWorld = () => import('@/components/HelloWorld')
const App = () => import("../App")
const Home = () => import("../components/Home")
const Mood = () => import("../components/Mood")
const MemoryDir = () => import("../components/MemoryDirectory")
const Memory = () => import("../components/Memory")
const TechnologyDirectory = () => import("../components/TechnologyDirectory")
const Technology = () => import("../components/Technology")
const Books = () => import("../components/Books")
const BlogMessage = () => import("../components/BlogMessage")
const AboutMe = () => import("../components/AboutMe")

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    // {
    //   path:'/',
    //   name: 'App',
    //   component: App
    // },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/mood',
      name: 'Mood',
      component: Mood
    },
    {
      path: '/memorydir',
      name: 'MemoryDir',
      component: MemoryDir
    },
    {
      path: '/memory/:id',
      name: 'Memory',
      component: Memory
    },
    {
      path: '/techdir',
      name: 'TechnologyDirectory',
      component: TechnologyDirectory
    },
    {
      path: '/technology/:id',
      name: 'Technology',
      component: Technology
    },
    {
      path: '/books',
      name: 'Books',
      component: Books
    },
    {
      path: '/blogmessage',
      name: 'BlogMessage',
      component: BlogMessage
    },
    {
      path: '/aboutme',
      name: 'AboutMe',
      component: AboutMe
    }
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // }
  ],
  mode:"history"
})



